<template>
	 <el-table
	      :data="bookData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
	      style="width: 100%">
	      </el-table-column>
	      <el-table-column
	        prop="bookname"
	        label="书名"
	        width="180">
	      </el-table-column>
	      <el-table-column
	        prop="bookauthor"
	        label="作者">
	      </el-table-column>
		  <el-table-column
		    prop="bookprice"
		    label="价格"
		    width="180">
	    </el-table>
	  </template>
				
	<el-pagination
	  background
	  layout="total, sizes, prev, pager, next, jumper"	
		@size-change="handleSizeChange"
	    @current-change="handleCurrentChange"
	    :current-page="currentPage"
	    :page-sizes="[10, 20, 30, 40]"
	    :page-size="pagesize"
		:total=parseInt(bookData.length)>
	</el-pagination>
	
</template>

<script>
 
	export default {
	    name: "search_book",
	    data(){
	        return{
	           bookData:[]
	        }
	    },
		methods:{
		   handleSizeChange(val) {
		    this.pagesize = val;
		      //        console.log(`每页 ${val} 条`);
		  },
		  handleCurrentChange(val) {
		    this.currentPage = val;
		  },
		  initBook(val){
			   this.loading = true;
			   let url="/book/basic/";
			   this.getRequest(url).then(resp => {
			                      this.loading = false;
			                      if (resp) {
			                          this.bookData=resp.data;
			                          //this.total = resp.total;
			                      }
			                  });
		  }
		},
		mounted() {
            this.initBook();
        }, 
	}
</script>

